import React, { Component } from 'react';
import { Container, Row } from "reactstrap";

//Import Section Title
import SectionTitle from "../common/section-title";
import BlogBox from "./blog-box";

//Import Images
import blog1 from "../../assets/images/blog/img-1.jpg";
import blog2 from "../../assets/images/blog/img-2.jpg";
import blog3 from "../../assets/images/blog/img-3.jpg";

class Blog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blogs : [
                { img : blog1, title : "Partner 1", category : "Business, Corporate", date : "25", month : "Jan", desc : "Description" },
                { img : blog2, title : "Partner 2", category : "Design, Designer", date : "12", month : "Feb", desc : "Description" },
                { img : blog3, title : "Partner 3", category : "Business, Corporate", date : "31", month : "Jun", desc : "Description" },
            ]
        }
    }
    
    render() {
        return (
            <React.Fragment>
                <section className="section" id="blog">
                    <Container>
                    <SectionTitle
                        title="Our Partners"
                        subtitle=" "
                        desc="Description"
                    />
                        <Row className="mt-5 pt-2">
                            <BlogBox blogs={this.state.blogs} />
                        </Row>
                    </Container>
                </section>
            </React.Fragment>
        );
    }
}

export default Blog;